/*! CSS Used from: Embedded */
a,
div,
h2,
h3,
p,
section {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
section {
  display: block;
}
*,
:after,
:before {
  box-sizing: inherit;
}
p {
  margin-bottom: 6px;
  margin-top: 6px;
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-weight: 300;
  transition: 150ms font-size, 150ms line-height;
  transition: none;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
}
p::before {
  display: none;
}
a {
  color: #ff5412;
  transition: color 150ms;
  text-decoration: none;
}
a:visited {
  color: #ff5412;
}
a:active,
a:hover {
  text-decoration: underline;
  color: #e44609;
}
.btn {
  margin: 16px 0;
}
.btn .el {
  margin-bottom: 6px;
  margin-top: 6px;
  font-family: "Roboto Mono", "Courier New", monospace;
  font-weight: 500;
  transition: 150ms font-size, 150ms line-height;
  transition: none;
  font-size: 16px;
  line-height: 24px;
  background-repeat: no-repeat;
  padding: 13px 24px;
  border: none;
  border-radius: 0;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  background-position: calc(100% - 24px) center;
  text-align: center;
  width: 100%;
  outline: 0;
}
.btn .el::before {
  display: none;
}
@media (min-width: 24em) {
  .btn .el {
    width: auto;
  }
}
.style-outline.btn .el.orange {
  background-color: transparent;
  color: #ff5412;
  border: 1px solid #ff5412;
  transition: background-color 150ms ease-out, color 150ms ease-out;
}
.style-outline.btn .el.orange:focus:not([disabled]),
.style-outline.btn .el.orange:hover:not([disabled]) {
  background-color: #fff;
  color: #e44609;
  border: 1px solid #e44609;
}
.style-outline.btn .el.orange:disabled {
  opacity: 0.5;
}
@media (min-width: 35.5em) {
  p {
    margin-bottom: 8px;
    margin-top: 8px;
    font-size: 18px;
    line-height: 24px;
  }
  .btn .el {
    margin-bottom: 8px;
    margin-top: 8px;
  }
}
.style-outline.btn .el {
  padding-left: 24px;
}
.bkg-light-gray-with-border {
  background-color: #fcfcfc;
  color: #474747;
  border: 1px solid #e7e7e7;
}
.bkg-light-gray-with-border h2.title::before,
.bkg-light-gray-with-border h2::before,
.bkg-light-gray-with-border h3::before {
  border-top-color: #007c97;
}
.std-shared-module {
  display: flex;
