/* CSS for third section of secondary main folks... */

/*! CSS Used from: Embedded */
a,
div,
h2,
img,
p,
section,
span {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
section {
  display: block;
}
*,
:after,
:before {
  box-sizing: inherit;
}
p {
  margin-bottom: 6px;
  margin-top: 6px;
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-weight: 300;
  transition: 150ms font-size, 150ms line-height;
  transition: none;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
}
p::before {
  display: none;
}
a {
  color: #ff5412;
  transition: color 150ms;
  text-decoration: none;
}
a:visited {
  color: #ff5412;
}
a:active,
a:hover {
  text-decoration: underline;
  color: #e44609;
}
.btn {
  margin: 16px 0;
}
.btn .el {
  margin-bottom: 6px;
  margin-top: 6px;
  font-family: "Roboto Mono", "Courier New", monospace;
  font-weight: 500;
  transition: 150ms font-size, 150ms line-height;
  transition: none;
  font-size: 16px;
  line-height: 24px;
  background-repeat: no-repeat;
  padding: 13px 24px;
  border: none;
  border-radius: 0;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  background-position: calc(100% - 24px) center;
  text-align: center;
  width: 100%;
  outline: 0;
}
.btn .el::before {
  display: none;
}
@media (min-width: 24em) {
  .btn .el {
    width: auto;
  }
}
.style-solid-iconless.btn .el.orange {
  background-color: #ff5412;
  border: 1px solid #ff5412;
  color: #fff;
  transition: background-color 150ms ease-out, color 150ms ease-out;
}
.style-solid-iconless.btn .el.orange:focus:not([disabled]),
.style-solid-iconless.btn .el.orange:hover:not([disabled]) {
  background-color: #e44609;
  border: 1px solid #e44609;
}
.style-solid-iconless.btn .el.orange:disabled {
  opacity: 0.5;
}
@media (min-width: 35.5em) {
  p {
    margin-bottom: 8px;
    margin-top: 8px;
    font-size: 18px;
    line-height: 24px;
  }
  .btn .el {
    margin-bottom: 8px;
    margin-top: 8px;
  }
}
.bkg-white {
  background-color: #fff;
  color: #474747;
}
.bkg-white h2.title::before,
.bkg-white h2::before {
  border-top-color: #007c97;
}
.js-lazy-bg-image {
  opacity: 0;
}
.js-lazy-bg-image.js-fade-in {
  opacity: 1;
  transition: opacity 0.5s;
}
.std-shared-module {
  display: flex;
  justify-content: center;
}
.std-shared-module .inner {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  padding: 0;
  margin: 0 auto;
  position: relative;
  min-width: 320px;
  width: 100%;
  max-width: 1280px;
  min-height: 400px;
}
@media (min-width: 80em) {
  .std-shared-module .inner {
    padding-left: 40px;
    padding-right: 40px;
    margin: 0 -40px;
    width: 1360px;
    max-width: inherit;
    min-height: 480px;
  }
}
.std-shared-module .row {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 24em) {
  .std-shared-module .row {
    padding-left: 40px;
    padding-right: 40px;
  }
}
.std-shared-module .content {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 64em) {
  .std-shared-module .row {
    flex-direction: row;
    align-items: flex-start;
  }
}
.sub-laurel {
  background: url(https://d38muu3h4xeqr1.cloudfront.net/website/static/WN-1548/images/stores/laurel-icon-dark.svg)
    center bottom/100% no-repeat;
  height: 60px;
  margin: 0 20px 10px;
  position: relative;
  width: 90px;
}
.sub-laurel .store-logo {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  top: -15px;
  transform: scale(0.5);
}
.sub-laurel .store-description,
.sub-laurel .store-title {
  position: absolute;
  text-align: center;
  font-size: 9px;
  line-height: 14px;
  width: 100%;
  padding: 0 10px;
}
@media (min-width: 35.5em) {
  .sub-laurel {
    height: 108px;
    width: 150px;
  }
  .sub-laurel .store-logo {
    top: 0;
    transform: scale(0.75);
  }
  .sub-laurel .store-description,
  .sub-laurel .store-title {
    font-size: 14px;
    line-height: 23px;
    padding: 0 15px;
  }
}
.sub-laurel .store-title {
  font-weight: 200;
  top: 24px;
}
@media (min-width: 35.5em) {
  .sub-laurel .store-title {
    top: 50px;
  }
}
@media (min-width: 64em) {
  .sub-laurel {
    height: 138px;
    margin: 0 20px;
    width: 200px;
  }
  .sub-laurel .store-logo {
